let sex=['不限','男','女'];
let qg=['不限','未婚','已婚'];
let job=['不限','教师','金融','法律','房地产','销售','服务类','IT'];
export default {
	data() {
		return {
			userPic:'../../static/demo/demo6.jpg',
			name:'焚心~',
			sex:'不限',
			qg:'不限',
			job:'不限'
		}
	},
	methods:{
		uimg(){//点击从相册或拍照选择头像
			uni.chooseImage({
				count:1,//上传一张
				sizeType:['compressed'],//original 原图，compressed 压缩图，默认二者都有
				//sourceType:album 从相册选图，camera 使用相机，默认二者都有。如需直接开相机或直接选相册，请只使用一个选项
				success(res){
					// console.log(res);//成功则返回图片的本地文件路径列表 tempFilePaths
					this.userPic=res.tempFilePaths;
				}
			})
		},
		//有问题，视图不更新
		changeOne(val){//单列如性别、情感、职业
		var arr=[];
			switch (val){
				case 'sex':
				arr=sex;
					break;
				case 'qg':
				arr=qg;
					break;
				case 'job':
				arr=job;
					break;
				default:
					break;
			}
			uni.showActionSheet({
				itemList:arr,
				success(res){
					var that=this;
					switch (val){
						case 'sex':
						that.sex=arr[res.tapIndex];
						console.log(that.sex);
							break;
						case 'qg':
						that.qg=arr[res.tapIndex];
							break;
						case 'job':
						that.job=arr[res.tapIndex];
							break;
						default:
							break;
					}
				}
			})
			console.log(this.sex)
			
		}
	}
}
